import HandimanModal from "../../../modal/hanimanModal";
import "./handiman.css";
import axios from "axios";
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import { useState, SyntheticEvent } from "react";
import { Typography, TextField, ButtonGroup, Button, Box } from "@mui/material";
import SingleHandiman from '../single/singleHandiman/singleHandiman';
import { useForm } from "react-hook-form";
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
function Handiman(): JSX.Element {

    const [lp,setLp]=useState("");
    const{register,handleSubmit, formState:{errors}}= useForm();
    let[car,setCar]= useState<HandimanModal>(null);
    const [loading, setLoading] = useState(false);
    const [show,setShow]=useState(undefined);

    const helloHandeler = () => {
        setLoading(true)
        setTimeout(() => {
        setLoading(false)
        }, 1000)
    
    };

    if (loading) return  <Box sx={{margin:10, display: 'flex' ,justifyContent: 'center' }}>
    <CircularProgress />
    </Box>

    const carURL = "https://data.gov.il/api/3/action/datastore_search?resource_id=c8b9f9c8-4612-4068-934f-d4acd2e3c06e&q=";
    const updateLp = (args: SyntheticEvent) => {
        let value = (args.target as HTMLInputElement).value;
        let correct=value.replaceAll("-","");
        setLp(correct);
    };

        const findCar = () => {
            helloHandeler();
            setShow(undefined);
            axios.get(carURL + lp).then((response) => {
            console.log(response)
            if(response.status<300){
            setCar(response.data.result.records[0]);
            if (response.data.result.records[0]== undefined){
                setShow(false);
            }
        } 
        
        }).catch(err=>{
            console.log(err);
        });
    };
    return (
        <div className="privateCar " dir="rtl" style={{textAlign: "center"}}>
        <Box
        sx={{
        width: 300,
        height: 300,
        textAlign: "center",
        display: 'inline-block',
        padding: 5,
        margin: 5,
    }}
    >
		<Typography className="headLine" variant="h6">     </Typography> <br /> 
        <form onSubmit={handleSubmit(findCar)}>
        <input type="text" placeholder="123-45-678"  name="licenseplate" className="licenseplate" onChange={updateLp} />
        <br /> <br />
        <ButtonGroup fullWidth>
            <Button  type="submit" variant="contained" color="info"  ><SearchIcon/></Button> &nbsp;
            <Button type="reset"  variant="contained" color="secondary" onClick={()=>{setCar(null); setShow(undefined)}}
        ><ClearIcon/></Button>
        </ButtonGroup>
        </form>
        <br />
        {car != null && <SingleHandiman handimanData={car} />}
        { ( show== false)  &&   <Stack sx={{ width: '100%',textAlign: "center" }} spacing={2}> 
        <Alert severity="info" >&nbsp; &nbsp;   </Alert>
        </Stack>}
        </Box>
        </div>
    );
}

export default Handiman;
