import "./truck.css";
import { Box, Button, ButtonGroup, TextField, Typography } from '@mui/material';
import { SyntheticEvent, useState } from "react";

import TruckModal from "../../../modal/truckModal";
import axios from "axios";
import SingleTruck from "../single/singleTruck/singleTruck";
import { useForm } from "react-hook-form";
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
function Truck(): JSX.Element {
    const [lp,setLp]=useState("");
    const{register,handleSubmit, formState:{errors}}= useForm();
    let[truck,setTruck]= useState<TruckModal>(null)
    const [loading, setLoading] = useState(false);
    const [show,setShow]=useState(undefined);

    const truckURL = "https://data.gov.il/api/3/action/datastore_search?resource_id=cd3acc5c-03c3-4c89-9c54-d40f93c0d790&q=";

    const helloHandeler = () => {
        setLoading(true)
        setTimeout(() => {
        setLoading(false)
        }, 1000)
    
    };

    if (loading) return  <Box sx={{margin:10, display: 'flex' ,justifyContent: 'center' }}>
    <CircularProgress />
    </Box>

    const updateLp = (args: SyntheticEvent) => {
    let value = (args.target as HTMLInputElement).value;
    let correct=value.replaceAll("-","");
    setLp(correct);
    };
    
    const findCar = () => {
        helloHandeler();
        setShow(undefined);
    axios.get(truckURL + lp).then((response) => {
        if(response.data.success== true){
        setTruck(response.data.result.records[0]);
        if (response.data.result.records[0]== undefined){
            setShow(false);
        }
        }
    }).catch(err=>{
    console.log(err);
    });
};


    return (
        <div className="privateCar " dir="rtl" style={{textAlign: "center"}}>
            <Box
        sx={{
        width: 300,
        height: 300,
        textAlign: "center",
        display: 'inline-block',
        padding: 5,
        margin: 5,
    }}
    >
		<Typography variant="h6">   3 </Typography> <br /> 
        <form onSubmit={handleSubmit(findCar)}>
        <input type="text" placeholder="123-45-678"  name="licenseplate" className="licenseplate" onChange={updateLp} />
        <br /> <br />
        <ButtonGroup fullWidth>
            <Button  type="submit" variant="contained" color="info"  ><SearchIcon/></Button> &nbsp;
            <Button type="reset"  variant="contained" color="secondary" onClick={()=>{setTruck(null); setShow(undefined)}}
        ><ClearIcon/></Button>
        </ButtonGroup>
        </form>
        <br />
        {truck != null && <SingleTruck truckData={truck} />}
        { ( show== false)  &&   <Stack sx={{ width: '100%',textAlign: "center" }} spacing={2}> 
        <Alert severity="info" >&nbsp; &nbsp;   </Alert>
        </Stack>}
            
        </Box>
        </div>
    );
}

export default Truck;
