import "./privateCar.css";
import { Box, Button, ButtonGroup, TextField, Typography } from '@mui/material';
import { SyntheticEvent, useState } from "react";
import PrivateCarModal from "../../../modal/privateCarModal";
import axios from "axios";
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import SingleCar from "../single/singleCar/singleCar";
import { useForm } from 'react-hook-form';
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
function PrivateCar(): JSX.Element {
    const{register,handleSubmit, formState:{errors}}= useForm();
    const [lp,setLp]=useState("");
    let[car,setCar]= useState<PrivateCarModal>(null)
    const [loading, setLoading] = useState(false);
    const [show,setShow]=useState(undefined);

    const helloHandeler = () => {
        setLoading(true)
        setTimeout(() => {
        setLoading(false)
        }, 1000)
    
    };

    if (loading) return  <Box sx={{margin:10, display: 'flex' ,justifyContent: 'center' }}>
    <CircularProgress />
    </Box>
    const carURL = "https://data.gov.il/api/3/action/datastore_search?resource_id=053cea08-09bc-40ec-8f7a-156f0677aff3&q=";

const updateLp = (args: SyntheticEvent) => {
let value = (args.target as HTMLInputElement).value;
let correct=value.replaceAll("-","");
setLp(correct);
};


const findCar = () => {
    helloHandeler();
    setShow(undefined);
    axios.get(carURL + lp).then((response) => {
    if(response.data.success== true){
        setCar(response.data.result.records[0]);
    if (response.data.result.records[0]== undefined){
        setShow(false);
    }
    }
    
    }).catch(err=>{
    console.log(err);
    });
};



    return (
        <div className="privateCar " dir="rtl" style={{textAlign: "center"}}>
	<Box
        sx={{
        width: 300,
        height: 300,
        textAlign: "center",
        display: 'inline-block',
        padding: 5,
        margin: 5,
    }}
    >
        <Typography className="headLine" variant="h6">
          
        </Typography>
        <br />
        <form onSubmit={handleSubmit(findCar)}>
        <input type="text" placeholder="123-45-678" minLength={5}  name="licenseplate" className="licenseplate" onChange={updateLp} />
        <br /> <br />
        <ButtonGroup fullWidth>
        <Button  type="submit" variant="contained" color="info"  ><SearchIcon/>
        </Button> &nbsp;
            <Button type="reset"  variant="contained" color="secondary" onClick={()=>{setCar(null); setShow(undefined)}}
        ><ClearIcon/></Button>
        </ButtonGroup>
        </form>
        <br />
        {(car != null )  && <SingleCar carData={car} />}
        { ( show== false)  &&   <Stack sx={{ width: '100%',textAlign: "center" }} spacing={2}> 
        <Alert severity="info" >&nbsp; &nbsp;   </Alert>
        </Stack>}
            
        </Box>
        </div>
    );
}

export default PrivateCar;
