import {Box, Typography, TextField, ButtonGroup, Button } from "@mui/material";
import SingleCar from "../single/singleCar/singleCar";
import "./bike.css";
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import BikeModal from "../../../modal/bikeModal";
import { SyntheticEvent, useState } from 'react';
import axios from "axios";
import SingleBike from "../single/singleBike/singleBike";
import { useForm } from "react-hook-form";
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';


function Bike(): JSX.Element {
    const[lp,setLp]=useState("")
    const[bike,setBike]=useState<BikeModal>(null);
    const{register,handleSubmit, formState:{errors}}= useForm();
    const bike_URL="https://data.gov.il/api/3/action/datastore_search?resource_id=bf9df4e2-d90d-4c0a-a400-19e15af8e95f&q=";
    const [show,setShow]=useState(undefined);
    const [loading, setLoading] = useState(false);

    const helloHandeler = () => {
        setLoading(true)
        setTimeout(() => {
        setLoading(false)
        }, 1000)
    
    };

    if (loading) return  <Box sx={{margin:10, display: 'flex' ,justifyContent: 'center' }}>
    <CircularProgress />
    </Box>
    
    const updateLp=(args:SyntheticEvent)=>{
        let value=(args.target as HTMLInputElement).value
        let correct=value.replaceAll("-","");
        setLp(correct);
    }
    const findBike=()=>{
        helloHandeler();
        setShow(undefined);
        axios.get(bike_URL+lp)
        .then(response=>{
            if(response.data.success== true){
            setBike(response.data.result.records[0])
            if (response.data.result.records[0]== undefined){
                setShow(false);
            }
            }
        }).catch(err=>{
            console.log(err);
        })

    }
    
    return (
        <div className="bike" dir="rtl" style={{textAlign: "center"}}>
        <Box
            sx={{
            width: 300,
            height: 300,
            textAlign: "center",
            display: 'inline-block',
            padding: 5,
            margin: 5,
        }}
    >
			<Typography variant="h6">  </Typography> <br /> <hr />
        <form onSubmit={handleSubmit(findBike)}>
        <input type="text" placeholder="123-45-678"  name="licenseplate" className="licenseplate" onChange={updateLp} />
        <br /> <br />
    
        <ButtonGroup fullWidth>
        <Button  type="submit" variant="contained" color="info"  ><SearchIcon/></Button> &nbsp;
            <Button type="reset"  variant="contained" color="secondary" onClick={()=>{setBike(null); setShow(undefined)}}
        ><ClearIcon/></Button>
        </ButtonGroup>
        </form>
        <br />
        {bike != null && <SingleBike bikeData={bike} />}
        { ( show== false)  &&   <Stack sx={{ width: '100%',textAlign: "center" }} spacing={2}> 
        <Alert severity="info" >&nbsp; &nbsp;   </Alert>
        </Stack>}
        </Box>
        </div>
    );
}

export default Bike;
