import "./singleCar.css";

import { PrivateCarModal } from "../../../../modal/privateCarModal";
import * as React from 'react';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
const bull = (
  <Box
    component="span"
    sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
  >
    
  </Box>
);

interface SingleCarProps {
	carData:PrivateCarModal
}

function SingleCar(props: SingleCarProps): JSX.Element {
  // to use moment format - npm install moment --save 
  var moment = require('moment'); // require
  var tokef = moment(props.carData.tokef_dt.split("T")[0], "YYYY/DD/mm");
  var resultTokef = tokef.format("mm/DD/YYYY");
  const card = (
    <React.Fragment>
      <CardContent>
        <Typography sx={{ fontSize: 14  }}  color="text.secondary" gutterBottom>
            - {props.carData.mispar_rechev}
        </Typography>
        <hr />
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}    - {props.carData.tozeret_nm}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}    - {props.carData.kinuy_mishari}  
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}     - {props.carData.shnat_yitzur}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}     - {resultTokef}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}     - {props.carData.tzeva_rechev}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
        {bull}     - {props.carData.sug_delek_nm}
        </Typography>
      </CardContent>
      <CardActions>
      
      </CardActions>
    </React.Fragment>
  );

    return (
      <div className="singleCar " dir="rtl">
        <br />
        <Box sx={{ minWidth: 275 }}>
      <Card variant="outlined">{card}</Card>
    </Box>
      </div>
    );
}

export default SingleCar;
